.player {
  .canvas {
    display: flex;
    border-radius: 6px;
    border: 4px solid black;
    margin-bottom: 16px;
    width: 100%;
    height: 300px;
    overflow: hidden;

    canvas {
      width: 100% !important;
      height: 100% !important;
      object-fit: cover;
    }
  }

  .controls {
    display: flex;
    flex-direction: column;
    gap: 12px;

    .microphoneControls {
      display: flex;
      flex-direction: column;
      gap: 8px;

      .microphoneBtn {
        padding: 12px 24px;
        background: #007bff;
        color: white;
        border: none;
        border-radius: 6px;
        cursor: pointer;
        font-size: 14px;
        font-weight: 500;
        transition: all 0.2s ease;

        &:hover:not(:disabled) {
          background: #0056b3;
        }

        &:disabled {
          opacity: 0.6;
          cursor: not-allowed;
        }

        &.active {
          background: #dc3545;

          &:hover:not(:disabled) {
            background: #c82333;
          }
        }

        &.loading {
          background: #6c757d;
          cursor: wait;
          
          &::after {
            content: '';
            display: inline-block;
            width: 12px;
            height: 12px;
            margin-left: 8px;
            border: 2px solid transparent;
            border-top: 2px solid white;
            border-radius: 50%;
            animation: spin 1s linear infinite;
          }
        }
      }

      .fullscreenBtn {
        padding: 12px 24px;
        background: #28a745;
        color: white;
        border: none;
        border-radius: 6px;
        cursor: pointer;
        font-size: 14px;
        font-weight: 500;
        transition: all 0.2s ease;

        &:hover {
          background: #218838;
        }

        &.active {
          background: #6c757d;

          &:hover {
            background: #5a6268;
          }
        }
      }

      .error {
        color: #dc3545;
        font-size: 12px;
        text-align: center;
        padding: 8px;
        background: #f8d7da;
        border: 1px solid #f5c6cb;
        border-radius: 4px;
      }

      .mobileTip {
        color: #856404;
        font-size: 12px;
        text-align: center;
        padding: 8px;
        background: #fff3cd;
        border: 1px solid #ffeaa7;
        border-radius: 4px;
      }

      .httpsWarning {
        color: #721c24;
        font-size: 12px;
        text-align: center;
        padding: 8px;
        background: #f8d7da;
        border: 1px solid #f5c6cb;
        border-radius: 4px;
        font-weight: 500;
      }
    }
  }

  // 全屏样式
  &.fullscreen {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: #000;
    z-index: 9999;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .canvas {
      border: none;
      margin-bottom: 0;
      flex: 1;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;

      canvas {
        max-width: 100%;
        max-height: 100%;
        width: 100vw !important;
        height: 100vh !important;
      }
    }

    .controls {
      position: absolute;
      top: 20px;
      right: 20px;
      background: rgba(0, 0, 0, 0.8);
      padding: 12px;
      border-radius: 8px;
      backdrop-filter: blur(10px);
      border: 1px solid rgba(255, 255, 255, 0.1);
      min-width: 200px;

      .microphoneControls {
        flex-direction: column;
        gap: 8px;

        .microphoneBtn,
        .fullscreenBtn {
          padding: 8px 16px;
          font-size: 12px;
          min-width: auto;
          white-space: nowrap;
        }
      }
    }
  }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
